import { IonicModule } from '@ionic/angular';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { ScrollingModule } from '@angular/cdk/scrolling';
import { ComboCategoryComponent } from './combo-category.component';
import { DetailCategoryProductsComponent } from './detail-category-products/detail-category-products.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
const routes: Routes = [
  {
    path: '',
    component: ComboCategoryComponent
  },
  {
    path: 'detail',
    component: DetailCategoryProductsComponent
  }, 
]


@NgModule({
  declarations: [ComboCategoryComponent ,DetailCategoryProductsComponent ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    ReactiveFormsModule.withConfig({warnOnNgModelWithFormControl: 'never'}),
    FormsModule,
    IonicModule,
    TranslateModule,
    ScrollingModule
  ]
})
export class ComboCategoryModule { }
